.projects {
  display: flex;
  flex-direction: column;
  height: 544px;
  width: 100%;
  margin: auto;
  text-align: center;
}
.project-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  margin-left: auto;
  margin-right: 10px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.project-button:hover {
  background-color: black;
  color: #facf0f;
}
.project--mainContent {
  display: flex;
  flex-direction: column;

  margin-top: -40px;
  text-align: left;
  margin-left: auto;
  margin-right: auto;

  width: 400px;
  height: 100%;
}
.content--one,
.content--two {
  width: 486px;
  height: 400px;
  /*border: 1px solid black;*/
  text-align: left;
  margin-left: 40px;
}

.projects p {
  font-size: 12px;
}
.aboutProject {
  width: 100%;
  height: 100%;

  /* border: 1px solid black;*/
}
.underline {
  width: 30px;
  height: 2px;
  background-color: #facf0f;
  margin-left: auto;
  margin-right: auto;
}
.container {
  /* border: 1px solid black;*/
  margin-left: auto;
  margin-right: 40px;

  display: flex;
  justify-content: center;
  column-gap: 20px;
  align-items: center;
  width: 100%;
  margin-top: 30px;

  height: 230px;

  border-radius: 10px;
}
.projectImage {
  height: 357px;
  width: 309px;
  margin-top: 30px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: 10px;
}
.imagedisplay {
  width: 100%;
  height: 176px;
  border: 4px solid black;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}
.projectContainer {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  width: 100%;

  display: flex;
  flex-wrap: wrap;
  position: relative;
  overflow-y: scroll;
  scroll-behavior: smooth;

  border-radius: 10px;
  overflow-x: hidden;
  cursor: all-scroll;
}
.header-project {
  position: absolute;
}
.image {
  width: 100%;
  height: 100%;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
input {
  border-style: none;
  border-bottom: 1px solid black;

  width: 200px;
  height: 30px;
}
.buttons {
  display: flex;
  column-gap: 30px;
}
.buttonClass {
  display: flex;
  justify-content: center;
  column-gap: 20px;
}
.searchButtons {
  background-color: #facf0f;
  width: 100px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;

  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .project-button {
    width: 60px;
    margin-right: 20px;
  }
  .about-button {
    width: 60px;
  }
  .container {
    width: 100%;
  }
}
