.about {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  margin: auto;
  text-align: center;
  margin-top: -20px;
}
.about-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin: auto;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.about-button:hover {
  background-color: #bca754;
}

.about--mainContent {
  display: flex;
  justify-content: center;
  column-gap: 15px;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  margin-top: 50px;
  margin-left: 10px;
  margin-right: auto;
  overflow-y: scroll;
  position: relative;

  scroll-behavior: smooth;

  /*border: 1px solid black;*/
}

.content--two {
  width: 450px;
  height: 200px;
  /* border: 1px solid black;*/
  text-align: left;
}
.content--one {
  text-align: left;
  width: 500px;
  height: 100%;
}

.skills {
  width: 100%;
}
.skills > button {
  margin: 5px;
  padding: 5px 20px 5px 20px;
  border-radius: 20px;
  border-color: transparent;
  box-shadow: 5px 5px 5px -2px;
}

.skills > button:hover {
  background-color: #facf0f;
  border-radius: 20px;
  border-color: transparent;
}

.about--heading {
  margin-top: 20px;
}

.about p {
  font-size: 12px;
}
.aboutMe {
  width: 100%;
  height: 200px;
}
.underline {
  width: 30px;
  background-color: #facf0f;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
